import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Content } from 'src/app/models/content';
import { AppGlobalsVariable } from 'src/app/shared/app-global-variable';
import { ApiService } from './api.service';

@Injectable({
  providedIn: 'root'
})
export class HomeService {

  formData:Content = new Content();
  constructor(private api: ApiService, private global: AppGlobalsVariable) { }

  public uploadImage(image: File): Observable<Response> {
    const formDataImage = new FormData();
    const httpHeader = new HttpHeaders({ 'Content-Type': 'multipart/form-data' });

    formDataImage.append('file', image, image.name);
    return this.api.post(this.global.sliderImage_upload, formDataImage, httpHeader);
  }

  public saveContentWithImage(content: Content) {
    let formData = new FormData();
    Object.entries(content).forEach(([key, value]) => {
      formData.append(key, value);
    });

    return this.api.post(this.global.saveOrUpdateContent, formData);
  }

  public saveContent(slider: Content) {
    return this.api.post(this.global.sliderContent_update, slider);
  }


  public getAllHomeContent(): Observable<Content[]> {
    return this.api.get(this.global.homeContent);
  }

  deleteContent(contentId: number) {
    return this.api.delete(this.global.contentDeleteById, contentId);
  }
}
