import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { HomeService } from 'src/app/core/services/home.service';
import { Content } from 'src/app/models/content';
import { EnumSection } from '../../enums.enum';
declare var $: any;

@Component({
  selector: 'app-what-makes-us-different',
  templateUrl: './what-makes-us-different.component.html',
  styleUrls: ['./what-makes-us-different.component.css']

})
export class WhatMakesUsDifferentComponent implements OnInit {
  @Input() Best!: Content[];
  loadCompleted: boolean = false;
  homeContents!: Content[];

  constructor(private homeService: HomeService) { }

  ngOnInit(): void {
    var _contents: Content[] = JSON.parse(localStorage.getItem("_contents") ?? "");
    this.Best = _contents.filter(x => x.homeIdFK == EnumSection.What_Makes_Us_Different);
    this.OnChanges();
  }

  OnChanges() {
    if (this.Best !== undefined)
      this.loadCompleted = true;
    else
      return;
  }

  getWhatIntroTitle() {
    return this.Best != undefined ? this.Best[0].title : "";
  }

  getWhatIntroDesc() {
    return this.Best != undefined ? this.Best[0].description : "";
  }

}
