import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { ContactUsService } from 'src/app/core/services/contact-us.service';
import { ContactUs } from 'src/app/models/contact-us';

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.css']
})
export class ContactUsComponent_Admin implements OnInit {

  constructor(private service: ContactUsService, private toastr: ToastrService) { }

  contactUsForm = new FormGroup({
    "Id": new FormControl(0)
    , "Introduction": new FormControl('', [Validators.required, Validators.max(500)])
    , "StreetAddress": new FormControl('', [Validators.required, Validators.maxLength(50)])
    , "City": new FormControl('', [Validators.required, Validators.maxLength(50)])
    , "State": new FormControl('', [Validators.required, Validators.maxLength(50)])
    , "ZipCode": new FormControl('')
    , "Country": new FormControl("", [Validators.required, Validators.maxLength(50)])
    , "Telephone1": new FormControl('', [Validators.required, Validators.maxLength(15)])
    , "Telephone2": new FormControl('')
    , "EmailAddress": new FormControl('', [Validators.required, Validators.maxLength(50)])
  });

  get f() { return this.contactUsForm.controls }
  submitted: Boolean = false;
  contactUs!: ContactUs;

  ngOnInit(): void {
    this.getContactUsInfo();
  }

  getContactUsInfo() {
    this.service.getContactUs()
      .subscribe(res => {
        if (res.statusCode === 200) {
          if (res.result != null || res.result != undefined) {
            this.contactUs = res.result;
            this.contactUsForm.patchValue(
              {
                "Id": this.contactUs.id
                , "Introduction": this.contactUs.introduction
                , "StreetAddress": this.contactUs.streetAddress
                , "City": this.contactUs.city
                , "State": this.contactUs.state
                , "ZipCode": this.contactUs.zipCode
                , "Country": this.contactUs.country
                , "Telephone1": this.contactUs.telephone1
                , "Telephone2": this.contactUs.telephone2
                , "EmailAddress": this.contactUs.emailAddress
              }
            );
          }
          console.log("respone_contact_us:", res);
        }
      });
  }

  onSubmit() {
    this.submitted = true;
    if (!this.contactUsForm.valid) return;
    this.service.saveContactUs(this.contactUsForm.value).subscribe((response) => {
      if (response.statusCode === 200) {
        this.toastr.success("Data saved successfully");
      }
    });
  }
}
