import { Component, OnInit, ɵɵNgOnChangesFeature } from '@angular/core';
import { Content } from 'src/app/models/content';
import { EnumContentType, EnumSection } from '../../enums.enum';

@Component({
  selector: 'app-what-we-offer',
  templateUrl: './what-we-offer.component.html',
  styleUrls: ['./what-we-offer.component.css']
})
export class WhatWeOfferComponent implements OnInit {

  constructor() { }
  introOffer!: Content;
  offers!: Content[];
  description: string = "";
  ngOnInit(): void {
    var _contents: Content[] = JSON.parse(localStorage.getItem("_contents") ?? "");
    this.offers = _contents.filter(x => x.homeIdFK == EnumSection.What_We_Offer);
    this.OnChanges();
  }

  OnChanges() {
    if (this.offers != undefined) {
      this.introOffer = this.offers.filter(x => x.contentType == EnumContentType[EnumContentType.IntroOffer])[0];
      let index = this.offers.indexOf(this.introOffer);
      this.offers.splice(index, 1);

      setTimeout(() => {
        let anchor: HTMLElement = document.getElementById('anchor_0') as HTMLElement;
        anchor.click();
      }, 500);

    }
  }

  explain(contentId: number) {
    let data = this.offers.filter(x => x.contentId == contentId)[0];
    if (data != undefined)
      this.description = this.offers.filter(x => x.contentId == contentId)[0].description;
    else
      this.description = "";
  }

}
