import { Injectable } from '@angular/core';
import { HappyCustomer } from 'src/app/models/happy-customer';
import { AppGlobalsVariable } from 'src/app/shared/app-global-variable';
import { ApiService } from './api.service';

@Injectable({
  providedIn: 'root'
})
export class HappyCustomerService {
  
  constructor(private api: ApiService, private global: AppGlobalsVariable ) { }
  
  getHappyCustomers() {
    return this.api.get(this.global.HappyCustomer_GetInfo)
  }
  public saveHappyCustomer(data: HappyCustomer) {
    return this.api.post(this.global.HappyCustomer_SaveInfo, data);
  }
  public updateHappyCustomer(data: HappyCustomer) {
    return this.api.post(this.global.HappyCustomer_UpdateInfo, data);
  }

}
