import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, NgForm, Validators } from '@angular/forms';
import { HomeService } from 'src/app/core/services/home.service';
import { Content } from 'src/app/models/content';
import { ToastrService } from 'ngx-toastr';
import { HttpEventType } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { AppGlobalsVariable } from 'src/app/shared/app-global-variable';
import { EnumSection } from 'src/app/shared/enums.enum';

@Component({
  selector: 'app-slider-cms',
  templateUrl: './slider-cms.component.html',
  styleUrls: ['./slider-cms.component.css']
})
export class SliderCmsComponent implements OnInit {
  get f() { return this.sliderForm.controls; }

  freezeRow: boolean = false;
  isSliderDataReady: boolean = false;
  sliderForm!: FormGroup;
  submitted: boolean = false;

  file!: File;
  height: string = "0";
  width: string = "0";
  imageSrc!: string;
  toggleActualDimension: string = "hidden";
  public progress?: number;
  highlightRow: Number = -1;


  @Input() sliderData?: Content[];

  constructor(public homeService: HomeService, private toastr: ToastrService, private global: AppGlobalsVariable) {
  }

  ngOnInit() {
    this.initSliderForm();

    var allContent:Content[] = JSON.parse(localStorage.getItem("AllContent") ?? "");
    this.sliderData = allContent.filter(x=>x.homeIdFK === EnumSection.Root)
    this.OnChanges();
  }
  initSliderForm() {
    this.sliderForm = new FormGroup({
      'ContentId': new FormControl(0)
      , 'Title': new FormControl('', [Validators.required, Validators.minLength(2), Validators.maxLength(50)])
      , 'Subtitle': new FormControl('', [Validators.required])
      , 'Description': new FormControl('', [Validators.required])

    });
  }

  OnChanges() {
    if (this.sliderData != undefined) {
      this.isSliderDataReady = true;

      let sliderPhotoRecord = this.sliderData.filter(x => x.graphicsURL != null && x.graphicsURL != undefined)[0];
      if (sliderPhotoRecord != null)
        this.imageSrc = this.global.uploadedImagePath + sliderPhotoRecord.graphicsURL;
    }
  }

  displayUpload() {
    return this.toggleActualDimension =='hidden' ? 'none' : '';
  }

  onFileChange(event: any) {
    const reader = new FileReader();

    if (event.target.files && event.target.files.length) {
      this.file = event.target.files[0];
      reader.readAsDataURL(this.file);
      reader.onload = rs => {
        this.imageSrc = reader.result as string;

        const img = new Image();
        img.src = URL.createObjectURL(this.file);
        img.onload = (e: any) => {
          this.height = e.path[0].height;
          this.width = e.path[0].width;
          this.toggleActualDimension = "visible";
        };

      }
    }

  }

  onSubmitSliderForm() {
    this.submitted = true;
    if (this.sliderForm.invalid) return;

    this.freezeRow = false;
    let content: Content = new Content();
    content.contentId = this.sliderForm.value.ContentId;
    content.title = this.sliderForm.value.Title;
    content.subTitle = this.sliderForm.value.Subtitle;
    content.description = this.sliderForm.value.Description;
    content.homeIdFK = EnumSection.Root;

    this.homeService.saveContent(content)
      .subscribe((res) => {
        let message = res.message;

        //when record is updated
        if (res.statusCode === 200 && content.contentId > 0) {
          let record = this.sliderData?.filter(x => x.contentId == content.contentId)[0];
          if (record != undefined) {
            record.title = this.sliderForm.value.Title;
            record.subTitle = this.sliderForm.value.Subtitle;
            record.description = this.sliderForm.value.Description;
          }
          this.toastr.success(message);
        }
        //when record was unable to update
        //204 = Status204NoContent
        else if (res.statusCode === 204 && content.contentId > 0) {
          this.toastr.error(message);
        }
        // when new record is saved
        else if (res.statusCode === 200 && content.contentId <= 0) {
          this.sliderData?.push(content);
          this.toastr.success(message);

        }
        //when new record was unable to save
        else {
          this.toastr.error(message);
        }
      });

  }

  uploadSliderImage() {
    this.homeService.uploadImage(this.file).subscribe(() => {
    });
  }

  resetForm() {
    this.sliderForm.reset();
    this.initSliderForm();
    this.submitted = false;
  }

  onRowEdit(contentId: number, rowIndex: number) {
    this.freezeRow = true;
    this.highlightRow = rowIndex;

    let selectedData = this.sliderData?.filter(x => x.contentId === contentId)[0];
    if (selectedData != undefined) {

      this.sliderForm.patchValue({
        'ContentId': selectedData.contentId,
        'Title': selectedData.title,
        'Subtitle': selectedData.subTitle,
        'Description': selectedData.description
      });
    }
  }
  onRowDelete(contentId: number) {

    if (confirm("Are you sure you want to delete this record?"))
      this.homeService.deleteContent(contentId).subscribe((response) => {
        if (response.statusCode === 200) {
          this.toastr.success("Record has been deleted", "Action Success");
        }
        else
          this.toastr.error("Oops! unable to delete record", "Action Failed")

      })
  }

  clickRow(rowIndex: number) {
    if (!this.freezeRow)
      this.highlightRow = rowIndex;
  }

}
