import { Component, OnInit } from '@angular/core';
import { ContactUsService } from 'src/app/core/services/contact-us.service';
import { ContactUs } from 'src/app/models/contact-us';


@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.css']
})
export class ContactUsComponent implements OnInit {
  latitude: number = 27.686076;
  longitude: number = 85.363061;
  zoom:number = 17;
  

  constructor(private service:ContactUsService) { }
  
  public contactUs:ContactUs = new ContactUs();
  ngOnInit(): void {
    this.setCurrentLocation();
    this.getContactUsInfo();
  }

  //Get Current Location Coordinates
  private setCurrentLocation() {
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.latitude = 27.686076;
        this.longitude = 85.363061;
        this.zoom = 17;
      });
    }
  }

  getContactUsInfo(){
    this.service.getContactUs().subscribe(res=> {
      if(res.statusCode === 200 && res.result != null && res.result != undefined){
        this.contactUs = res.result
      }
    })
  }
}
