
import { Component, Input, OnInit } from '@angular/core';
import { Content } from 'src/app/models/content';
import { EnumContentType, EnumSection } from '../../enums.enum';

@Component({
  selector: 'app-hot-package',
  templateUrl: './hot-package.component.html',
  styleUrls: ['./hot-package.component.css']
})
export class HotPackageComponent implements OnInit {
  @Input() hotPackage!: Content;

  constructor() { }

  ngOnInit(): void {
    var _contents: Content[] = JSON.parse(localStorage.getItem("_contents") ?? "");
    this.hotPackage = _contents.filter(x => x.homeIdFK == EnumSection.Packages && x.contentType == EnumContentType[EnumContentType.HotPackage])[0];
  }

  ngOnChanges() {
  }
  
  getTitle() {
    if (this.hotPackage != undefined)
      return this.hotPackage.title;
    return "";
  }

  getDescription() {
    if (this.hotPackage != undefined)
      return this.hotPackage.description;
    return "";
  }

  

}

