import { componentFactoryName } from '@angular/compiler';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminLayoutComponent } from './core/layout/admin-layout/admin-layout.component';
import { GuestLayoutComponent } from './core/layout/guest-layout/guest-layout.component';
import { InternalServerComponent } from './error-pages/internal-server/internal-server.component';
import { DashboardComponent } from './modules/admin/dashboard/dashboard.component';
import { HomePageComponent } from './modules/admin/home-page/home-page.component';
import { AboutUsComponent } from './modules/guest/about-us/about-us.component';
import { AboutUsComponent_Admin } from './modules/admin/about-us/about-us.component';
import { ContactUsComponent } from './modules/guest/contact-us/contact-us.component';
import { HomeComponent } from './modules/guest/home/home.component';
import { LoginComponent } from './modules/guest/login/login.component';
import { ContactUsComponent_Admin } from './modules/admin/contact-us/contact-us.component';
import { GalleryComponent_Admin } from './modules/admin/gallery/gallery.component';
import { HappyCustomerAdminComponent } from './modules/admin/home-page/components/happy-customer-admin/happy-customer-admin.component';

const routes: Routes = [
  {
    path: '',
    component: GuestLayoutComponent,
    children: [
      { path: '', component: HomeComponent, pathMatch: 'full' },
      { path: 'contactus', component: ContactUsComponent, pathMatch: 'full' },
      { path: 'aboutus', component: AboutUsComponent, pathMatch: 'full' },
      { path: 'login', component: LoginComponent, pathMatch: 'full' }
    ]
  },

  {
    path: 'admin',
    component: AdminLayoutComponent,
    children: [
      { path: '', component: DashboardComponent, pathMatch: 'full' },
      { path: 'home', component: HomePageComponent, pathMatch: 'full' },
      { path: 'aboutus', component: AboutUsComponent_Admin, pathMatch: 'full' },
      { path: 'gallery', component: GalleryComponent_Admin, pathMatch: 'full' },
      { path: 'contactus', component: ContactUsComponent_Admin, pathMatch: 'full' },
    ]
  },
  { path: '500', component: InternalServerComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
