import { Component, Input, OnInit } from '@angular/core';
import { Content } from 'src/app/models/content';
import { AppGlobalsVariable } from '../../app-global-variable';
import { EnumSection } from '../../enums.enum';

@Component({
  selector: 'popular-packages',
  templateUrl: './popular-packages.component.html',
  styleUrls: ['./popular-packages.component.css']
})
export class PopularPackagesComponent implements OnInit {

  constructor(public _global: AppGlobalsVariable) { }
  @Input() allPackages!: Content[]
  packageIntro: Content = new Content();
  global:any;
  ngOnInit(): void {
    this.global = this._global;
    var _contents: Content[] = JSON.parse(localStorage.getItem("_contents") ?? "");
    this.allPackages = _contents.filter(x => x.homeIdFK == EnumSection.Packages);
    this.OnChanges();
  }

  OnChanges(){
    if(this.allPackages != undefined && this.allPackages.length > 0){
      this.packageIntro = this.allPackages.filter(x=>x.graphicsURL == null)[0];
      if(this.packageIntro != undefined){
        let index = this.allPackages.indexOf(this.packageIntro);
        this.allPackages.splice(index,1);
      }
    }
  }
}
