import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ContactUs } from 'src/app/models/contact-us';
import { AppGlobalsVariable } from 'src/app/shared/app-global-variable';
import { ApiService } from './api.service';

@Injectable({
  providedIn: 'root'
})
export class ContactUsService {
  constructor(private api: ApiService, private global: AppGlobalsVariable) {}

  public saveContactUs(data: ContactUs) {
    return this.api.post(this.global.ContactUs_SaveInfo, data);
  }

  public getContactUs(){
    return this.api.get(this.global.ContactUs_GetInfo);
  }

}
