import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Content } from 'src/app/models/content';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private http: HttpClient) { }
  baseUrl: string = environment.apiBaseUrl;

  // formatErrors(error: any) {
  //   return throwError(error.error);
  // }

  //http get method.
  get(path: string, params?: HttpParams): Observable<any> {
    var _path = this.baseUrl + path;
    return this.http.get(_path, { params });
    //.pipe(catchError(this.formatErrors));
  }

  //http post method
  post(path: string, body: Object = {}, header?: HttpHeaders): Observable<any> {
    var _path = this.baseUrl + path;
    return this.http.post(_path, body);

  }

  delete(path: string, id: number, header?: HttpHeaders): Observable<any> {
    var _path = this.baseUrl + path + `/${id}`;
    return this.http.post(_path,null);
  }

}
